/*
* Icons
*/

$icon-font-url: '~fonts/uncanny-automator';

@font-face {
	font-family: 'Uncanny Automator Icons';
	src:
		url( $icon-font-url + '.ttf' ) format( 'truetype' ),
		url( $icon-font-url + '.woff' ) format( 'woff' ),
		url( $icon-font-url + '.svg' ) format( 'svg' );
		font-weight: normal;
		font-style: normal;
}

.uap .uo-icon {
	font-family: 'Uncanny Automator Icons' !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

	.uo-icon--loading {
		&:before {
			content: $uo-icon-loading; 
		}
	}

